<template>
  <div>
    <div v-if="!showEdit && !showAdd" class="container">
      <div class="header">
        <div class="title">公司列表</div>
        <el-button style="margin-left: 30px" @click="showAdd=!showAdd">添加公司</el-button>
      </div>
      <ceres-table
        :pagination="pagination"
        :table-column="tableColumn"
        :table-data="tableData"
        :operate="operate"
        @handleEdit="handleEdit"
        @handleDelete="handleDelete"
        @currentChange = "currentChange"
      />
    </div>

    <company-add v-if="showAdd" @hanleHide="handleHideAdd"/>
    <company-edit v-if="showEdit" :edit-i-d="editID" @hanleHide="handleHideEdit"/>
  </div>
</template>

<script type="text/ecmascript-6">
import CompanyModel from '@/models/company'
import CompanyAdd from './CompanyAdd'
import CompanyEdit from './CompanyEdit'

export default {
  name: 'CompanyList',
  components: {
    CompanyAdd,
    CompanyEdit
  },
  data() {
    return {
      tableColumn: [
        { prop: 'name', label: '公司名' },
        { prop: 'manager', label: '负责人' },
      ],
      tableData: [],
      operate: [
        { name: '编辑', func: 'handleEdit', type: 'primary' },
        { name: '删除', func: 'handleDelete', type: 'danger' },
      ],
      showEdit: false,
      showAdd: false,
      pagination: {
        pageSize: 0,
        pageTotal: 0
      },
      editID: ''
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getCompanyList(1, 10)
  },
  methods: {
    async getCompanyList(page, size) {
      const res = await CompanyModel.getList(size, page)
      this.tableData = [...res.items]
      this.pagination.pageTotal = res.total
      this.pagination.pageSize = res.size
    },
    handleEdit(val) {
      this.showEdit = true
      this.editID = val.row.id
    },
    async handleDelete(val) {
      const res = await CompanyModel.deleteOne(val.row.id)
      this.$message.success('删除成功')
      this.getCompanyList(1, 10)
    },
    handleHideAdd() {
      this.showAdd = false
      this.getCompanyList(1, 10)
    },
    handleHideEdit() {
      this.showEdit = false
      this.getCompanyList(1, 10)
    },
    // 切换当前页
    currentChange(page) {
      this.getCompanyList(page, 10)
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.container {
  .header {
    display flex
    align-items center
    .title {
      height 59px
      line-height 59px
      font-size 16px
      font-weight 500
    }
  }
}
</style>
